import {Component, OnInit} from '@angular/core';
import {NavController} from '@ionic/angular';
import {AfricleanControlAuthenticationProvider} from 'src/providers/africlean-control-fields/africlean-control-authentication.provider';
import {TranslateService} from '@ngx-translate/core';
import {AfricleanHttpUserProvider} from 'src/providers/africlean-http/africlean-http-user.provider';
import {Router, ActivatedRoute} from '@angular/router';
import {AfricleanSessionProvider} from 'src/providers/africlean-session/africlean-session.provider';
import {AfricleanObservablesProvider} from 'src/providers/africlean-observables/africlean-observables.provider';
import {AfricleanWebsocketProvider} from 'src/providers/africlean-websocket/africlean-websocket.provider';
import {AfricleanModalProvider} from 'src/providers/africlean-modal/africlean-modal.provider';
import {AfricleanForgotPasswordComponent} from 'src/app/components/africlean-forgot-password/africlean-forgot-password.component';
import {AfricleanConfigProvider} from 'src/providers/africlean-config/africlean-config.provider';

@Component({
    selector: 'page-authentication',
    templateUrl: './authentication.page.html',
    styleUrls: ['./authentication.page.scss'],
})
/**
 * Page d'authentification iOS via SECmobile
 */
export class AuthenticationPage implements OnInit {

    /**
     * Message d'erreur à afficher si un problème d'authentification survient
     */
    public errorMsg: string;

    /**
     * Etat du spinner de chargement
     */
    public hideSpinner = true;

    /**
     * Identifiant de l'utilisateur - phone number
     */
    public login: string;

    /**
     * Mot de passe
     */
    public password: string;

    /**
     * Afficher un message de confirmation une fois enregistrer
     */
    public displayRegistrationConfirmation = false;

    public counts: any[];
    private phoneCode = '224';

    /**
     * Teste la platform
     */
    public isWeb: boolean;

    /**
     * Constructeur de la page
     */
    constructor(private navCtrl: NavController,
                private controlField: AfricleanControlAuthenticationProvider,
                private translateService: TranslateService,
                private httpUser: AfricleanHttpUserProvider,
                private route: ActivatedRoute,
                private session: AfricleanSessionProvider,
                private observableProvider: AfricleanObservablesProvider,
                private websocket: AfricleanWebsocketProvider,
                private config: AfricleanConfigProvider,
                private modal: AfricleanModalProvider) {
    }

    /**
     * Initialisation de la page
     */
    ngOnInit(): void {
        // Test la platforme
        this.isWeb = this.config.isWeb();

        this.route.queryParams.subscribe((params) => {
            this.displayRegistrationConfirmation = params.displayRegistrationConfirmation;
        });
    }

    /**
     * Lance l'authentification
     */
    public sendAuthent() {
        const fields = {
            login: this.login,
            password: this.password
        };

        const result = this.controlField.authentication(fields);

        this.hideSpinner = result.hideSpinner;

        if (result.message != null) {
            this.getMessage(result.message);
        } else {
            this.errorMsg = result.message;
            fields.login = '+' + this.phoneCode + ' ' + this.login.replace(' ', '');
            this.httpUser.login('login/login-user', fields.login, fields.password).subscribe(
                (user) => {
                    this.goToHome(user);
                }
            );
        }
    }


    /**
     * Renvoie le bon message
     * @param message message à afficher
     */
    private getMessage(message: string) {
        this.translateService.get(message).subscribe((res: string) => {
            this.errorMsg = res;
        });
    }

    /**
     * Permet de créer une session pour l'utilisateur
     */
    public setSession() {

    }

    /**
     * Rédirige vers l'inscription
     */
    public goRegistration() {
        this.navCtrl.navigateForward('registration');
    }

    /**
     * Rédirige vers la récuperation du mot de passe
     */
    public async forgotPassword() {
        await this.modal.create(AfricleanForgotPasswordComponent, 'my-custom-modal-css');
    }

    /**
     * Récupère le compte correspondant à l'espace
     * @param space Rôle
     */
    public goTo(space: string) {

        let userId;
        let role;

        this.counts.forEach(userLog => {
            if (userLog.role === space) {
                userId = userLog.id;
                role = userLog.role;
            }
        });

        this.httpUser.loged('login/user-loged/', userId).subscribe(
            (user) => {
                this.setSessionVariables(user, role).then(
                    (_) => {
                        // Signale une nouvelle session
                        this.observableProvider.isAuth.emit(true);
                        this.navCtrl.navigateForward('home');
                    }
                );
            }
        );
    }

    /**
     * Renseigne les variables de session
     * @param user Utilisateur connecté
     * @param role
     */
    private async setSessionVariables(user: any, role: number) {
        await this.session.setItem('user', user);
        await this.session.setItem('userId', user.id);
        await this.session.setItem('login', user.phoneNumber);
        await this.session.setItem('password', user.password);
        await this.session.setItem('role', role);
        await this.session.setItem('isSubscribe', user.isSubscribe);
    }

    /**
     * Rédirige l'utilisateur vers le bon espace
     * @param userLog User log
     */
    private goToHome(userLog: any[]) {
        if (userLog.length === 0) {
            // le compte n'existe pas
            this.getMessage('PAGES.AUTHENTICATION.ERROR.NOT_EXIST_COUNT');
            this.hideSpinner = true;
        }
        if (userLog.length > 0) {
            const role = userLog[0].role;
            // Dans le cas ou l'utilisateur à un seul compte
            if (userLog.length === 1) {
                const user = userLog[0].user;
                this.setSessionVariables(user, role).then(
                    (_) => {
                        // Signale une nouvelle session
                        this.observableProvider.isAuth.emit(true);
                        this.navCtrl.navigateForward('home');
                    }
                );

                /*  this.httpUser.loged('login/user-loged/', userLog[0].id).subscribe(
                      (user) => {
                          this.setSessionVariables(user, role).then(
                              (_) => {
                                  // Signale une nouvelle session
                                  this.observableProvider.isAuth.emit(true);
                                  this.navCtrl.navigateForward('home');
                              }
                          );
                      }
                  );*/
            }

            // Dans le cas ou l'utilisateur à deux comptes. Donc il faut donner le choix
            // a l'utilisateur de choisir entre les deux comptes
            if (userLog.length === 2) {
                // donner le choix
                this.counts = userLog;

            }

        }
    }

    /**
     * Envoi le userId au serveur
     */
    public async setPseudoToServer() {
        const userId = await this.session.getItem('userId');
        this.websocket.emit('newClient', userId);
        this.websocket.receive('user_state').subscribe(
            (res) => {
            });
    }

    private _filter(value: string, list: any[]): any[] {
        const filterValue = value;
        return list.filter(option => option.dialCode.indexOf(filterValue) === 0);
    }

    telInputObject(obj) {
        const list = obj.p;
        const country = this._filter(this.phoneCode.replace('+', ''), list);
        if (country.length > 0) {
            obj.setCountry(country[0].iso2);
            this.phoneCode = country[0].dialCode;
        }
    }

    getNumber(event) {
    }

    onCountryChange(event: any) {
        this.phoneCode = event.dialCode;
    }
}
